import React from 'react'
import { NavLink } from 'react-router-dom'

const Navbar = () => {
  return (
    <>
    <div className="container-fluid nav_bg">
      <div className="row">
      <div className="col-10 mx-auto mt-2">
      <nav className="navbar navbar-expand-lg navbar-light ">
        <div className="container-fluid">
          <NavLink className="navbar-brand" to="/">MyWeb</NavLink>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink activeClassName="menu_active" exact className="nav-link active" aria-current="page" to="/">Home</NavLink>
              </li>             
              <li className="nav-item">
                <NavLink activeClassName="menu_active" className="nav-link " aria-current="page" to="/About">About</NavLink>
              </li>             
              <li className="nav-item">
                <NavLink activeClassName="menu_active" className="nav-link" aria-current="page" to="/Service">Our Services</NavLink>
              </li>             
              <li className="nav-item">
                <NavLink activeClassName="menu_active" className="nav-link" aria-current="page" to="/Contact">Contact Us</NavLink>
              </li>             
            </ul>
          </div>
        </div>
      </nav>
    </div>
    </div>
    </div>
    </>
  )
}

export default Navbar
